.App {
    height: 100vh;
    display: flex;
    flex-direction: column;
}

.main {
    flex: 1 1;
    margin-top: 5em;
    padding: 2em 15em;
}

.AppToolbar-root {
    display: flex;
    position: relative;
    align-items: center;
}

.AppToolbar-gutters {
    padding-left: 1em;
    padding-right: 1em;
}

@media (min-width:600px) {
    .AppToolbar-gutters {
        padding-left: 1.5em;
        padding-right: 1.5em;
    }
}

.AppToolbar-regular {
    min-height: 3.5em;
}

@media (min-width:0px) and (orientation: landscape) {
    .AppToolbar-regular {
        min-height: 3.125em;
    }
}

@media (min-width:600px) {
    .AppToolbar-regular {
        min-height: 4.375em;
    }
}

.sticky {
    display: flex;
    justify-content: space-between;
    position: fixed;
    z-index: 1;
    width: 100%;
}

.AppToolbar-dense {
    min-height: 3em;
}

.app1 {
    border-bottom: 1px solid rgba(255, 255, 255, 0.12);
}

.app2 {
    justify-content: space-between;
}

.menu1 {
    padding: 0.5em;
    flex-shrink: 0;
}

.App-UnderlineHover {
    text-decoration: none;
}

.App-ColorInherit {
    color: inherit;
}

.App-NoWrap {
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}

.Menu-Body {
    font-size: 0.875rem;
    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
    font-weight: 400;
    line-height: 1.43;
    letter-spacing: 0.01071em;
}

.footer li,
.footer ul {
    padding: 0;
    margin: 0;
    list-style: none;
}

.socialShareIcon:hover .facebook-svg ellipse {
    fill: #0077fb;
}

.socialShareIcon:hover .twitter-svg ellipse {
    fill: #1e90ff;
}

.socialShareIcon:hover .youtube-svg #Oval {
    fill: red;
}

.socialShareIcon:hover .instagram-svg circle {
    fill: #e1306c;
}